* {
    margin: 0;
    padding: 0;
}

.header {
    position: sticky;
    z-index: 1;
    top: 0;
    background: #FFF;
    box-shadow: 12px 4px 24px 0px rgba(0, 0, 0, 0.06);
}

.header__container {
    margin: 0 auto;
    max-width: 1140px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__icon {
    display: flex;
    align-items: center;
    gap: 14px;
}

.header__icon h4 {
    max-width: 152px;
    font-family: 'Courier New', Courier, monospace;
    color: #000;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
}