.LoginPage {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #FFF;
  height: 100%;
  padding-bottom: 101px;
}

.login__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  padding: 42px;
  border-radius: 10px;
}

.login__left h4 {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  text-align: center;
  color: #000;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
}

.login__left p {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 18px;
  line-height: 23px;
  margin-bottom: 20px;
  text-align: center;
  color: #304FFF;
}

.login__left form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

.login__left span {
  color: #787993;
  text-align: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin-bottom: 8px;
}

.login__left input[type="email"],
.login__left input[type="password"] {
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.login__left button {
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 20px;
  border-radius: 5px;
  background: #41D481;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login__left button:hover {
  background-color: #1d9f81;
}

.login__left a:last-of-type {
  color: #4E72CA;
  margin-top: 10px;
}

.login__left span:last-of-type {
  margin-top: 20px;
}

.login__right img {
  width: 400px;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: white;
  padding: 20px;
  text-align: center;
}