.product-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
}
.parent-card{
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
}
.time {
    color: #4A4A4A;
    font-family: monospace;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding-bottom: 10px;
    opacity: 0.8;
}

.top-card__title {
    display: flex;
    align-items: center;
    gap: 77px;
}

.top-card__title h2:nth-child(1) {
    border-bottom: 2px solid #4E72CA;
    cursor: pointer;
    padding-bottom: 10px;
    transition: .3 all;
    color: #4E72CA;
    margin: 0;

}

.top-card__title h2 {
    color: #9B9B9B;
    cursor: pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 24px;
    margin: 0;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.top-card__title h2:hover {
    color: #4E72CA;
    border-bottom: 2px solid #4E72CA;
    transition: .3s all;
    cursor: pointer;
    padding-bottom: 10px;
}

.card {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 10px 10px 0 10px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    overflow: hidden;
}

.card-img-top {
    height: 200px;
    object-fit: cover;
}

.card-body {
    padding: 20px;
}

.card-title {
    font-size: 24px;
    margin-bottom: 10px;
}

.card-text {
    font-size: 16px;
    margin-bottom: 20px;
}

.btn-primary {
    background-color: #007bff;
    border-color: #007bff;
    font-size: 16px;
    padding: 10px 20px;
    border-radius: 5px;
    color: white;
    text-decoration: none;
}

.btn-primary:hover {
    background-color: #0069d9;
    border-color: #0062cc;
}

.card-img-top {
    object-fit: cover;
    border-radius: 5px 5px 0 0;
}

.card-title {
    font-family: monospace;
    font-size: 20px;
    margin-bottom: 10px;
    color: #333;
}

.card-text {
    font-size: 12px;
    margin-bottom: 20px;
    color: #666;
}

.parent-card {
    overflow: hidden;
    border-bottom: 2px solid silver;
    padding-bottom: 40px;
    
}
.show-more{
   text-align: center;
}
.show-more h5{
    cursor: pointer;
    padding-top: 18px;
    color: #4E72CA;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding-bottom: 74px;
}

.Card-scroll{
    background: rgba(182, 202, 251, 0.16);
    box-shadow: 0px 21px 13px 0px rgba(182, 202, 251, 0.23) inset;
}

.show-more,
.show-less {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.show-more button,
.show-less button {
    color: #4E72CA;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  background-color: transparent;
}


.recently_viewed{
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
}

.SocNet{
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
}

.Footer{
    width: 100%;
    height: 100%;
    background: #33383C;
}
