.container {
  max-width: 1140px;
  margin: 0 auto;
  margin-top: 50px;
}

.row {
  display: flex;
  align-items: center;
  gap: 40px;
  padding: 20px 0 50px 0;
}

.col-md-4 {
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
}

.img-fluid {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.product-info {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-info h3 {
  font-size: 24px;
  margin-bottom: 5px;
}

.product-info p {
  font-size: 20px;
  margin-bottom: 10px;
}

.product-info span {
  font-size: 18px;
  color: #666;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.spinner {
  border: 10px solid #f3f3f3;
  border-top: 10px solid #007bff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.product__title{

   font-size: 56px;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   margin-bottom: 25px;
   font-weight: 600;
}
.desc{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 15px;
}

.lead{
  font-weight: bold;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: red;
}